<script type="text/javascript">
// 定义一些公共的属性和方法
const configKey = function (key) {
    let g = this.config;
    for (let k in g) {
        const item = g[k];
        if (item.key == key) {
            return item.value;
        }
    }
    return null;
};

const round = function (num, intLen = 2, dotLaterLen = 6) {
    let z = "000000000000000000000000000000";
    let zn = Number(1 + z.substr(0, dotLaterLen));
    num = Math.round(num * zn) / zn;
    let s = String(num);

    if (s.indexOf(".") !== -1) {
        let a = s.split(".");
        let n1 = a[0],
            n2 = a[1],
            zn;

        if (a[0].length < intLen) {
            n1 = z.substr(0, intLen - a[0].length) + a[0];
        }

        if (a[1].length < dotLaterLen) {
            zn = z.substr(0, dotLaterLen - a[1].length);
            n2 = String(a[1]) + zn;
        }

        let res = `${n1}.${n2}`;
        return res;
    }

    let zl = z.substr(0, dotLaterLen);
    return `${s}.${zl}`;
};

let getQueryVariable = function (variable) {
    // var query = window.location.search.substring(1);
    var query = window.location.hash.substring(1);
    query = query.substring(query.lastIndexOf("?") + 1);
    var vars = query.split("&");
    for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
            return pair[1];
        }
    }
    return false;
};

let config = {};
let apiUrl = "/";
let webUrl = "/";
let wsUrl = "/";
const container = {
    span: 16,
    offset: 4,
}

// 暴露出这些属性
export default {
    // var
    config,
    apiUrl,
    webUrl,
    wsUrl,
    // fn
    configKey,
    round,
    container,
    getQueryVariable,
};
</script>