<script>
import Head from "@/components/Head.vue";
import Foot from "@/components/Foot.vue";
import zhCN from "ant-design-vue/lib/locale-provider/zh_CN";
import enUS from "ant-design-vue/lib/locale-provider/en_US";

// import moment from 'moment';
// import 'moment/locale/zh-cn';
// moment.locale('en');

export default {
    name: "Home",
    props: {
        msg: String,
    },
    components: {
        Head,
        Foot,
    },
    data() {
        return {
            locale: enUS,
            loading: true,
        };
    },
    methods: {
        init() {
            this.loading = true;
            $.get(configUrl, (res) => {
                this.loading = false;

                if (res.flag != "ok") {
                    alert(res.flag);
                    return;
                }
                this.g.config = res.data;
                this.g.apiUrl = this.g.configKey("api_url");
                this.g.webUrl = this.g.configKey("web_url");
                this.g.wsUrl = this.g.configKey("ws_url");
                $("title").text(this.g.configKey("web_name"));
            });
        },
    },
    mounted() {
        this.init();
    },
};
</script>

