<template>
    <div class="Head">
        <div class="t-left">
            <a-row type="flex">
                <a-col :span="12">
                    <router-link to="/"><img alt="Vue logo" src="//assets99.oss-cn-hongkong.aliyuncs.com/images/logo/logo-30.png" height="64px" style="margin-right:20px;"></router-link>

                    <router-link to="/">Home</router-link>
                    <a-divider type="vertical" />
                    <router-link to="/market">Market</router-link>

                    <a-divider type="vertical" />
                    <router-link to="/futures?symbol=btcusdt">Futures</router-link>

                    <!-- <a-divider type="vertical" /> -->
                    <!-- <router-link to="/finance">Finance</router-link> -->

                    <a-divider type="vertical" />
                    <router-link to="/startup-list">Startup</router-link>

                </a-col>
                <a-col :span="12" class="t-right">
                    <!-- 未登录 -->
                    <span v-if="!user_info">
                        <router-link to="/login">Login</router-link>
                        <a-divider type="vertical" />
                        <router-link to="/register">Sign Up</router-link>
                    </span>
                    <!-- 登录后 -->
                    <span v-if="user_info">
                        <a-dropdown :trigger="['click']">
                            <a class="ant-dropdown-link" @click="e => e.preventDefault()" style="color:#007bff;">
                                <a-icon type="user" class="icon-style" /> <span>{{user_info.account}}</span>
                            </a>
                            <a-menu slot="overlay">
                                <a-menu-item v-for="i in my_menus" :key="i.name">
                                    <router-link :to="i.url">
                                        <span>
                                            {{i.name}}
                                        </span>
                                    </router-link>
                                </a-menu-item>
                            </a-menu>
                        </a-dropdown>
                    </span>
                    <!-- 语言菜单 -->
                    <a-divider type="vertical" />
                    <a-dropdown :trigger="['click']">
                        <a class="ant-dropdown-link" @click="e => e.preventDefault()" style="color:#007bff;">
                            <a-icon type="global" class="icon-style" /> <span>Language</span>
                        </a>
                        <a-menu slot="overlay">
                            <a-menu-item v-for="i in lang" :key="i.name">
                                <a href="#"><img :src="i.img" alt="image" height="18"> {{i.name}}</a>
                            </a-menu-item>
                        </a-menu>
                    </a-dropdown>

                </a-col>
            </a-row>

        </div>
    </div>
</template>

<script>
export default {
    name: "Head",
    props: {
        msg: String,
    },
    data() {
        return {
            lang: [
                { name: "English", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/gb.svg' },
                { name: "Français", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/fr.svg' },
                { name: "Indonesian", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/in.svg' },
                { name: "한국인", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/kr.svg' },
                { name: "日本語", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/jp.svg' },
                { name: "繁體中文", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/hk.svg' },
            ],
            my_menus: [
                { name: "My Assets", url: "/my/assets" },
                { name: "Contract Account", url: "/my/contract" },
                { name: "Withdraw", url: "/my/withdraw" },
                { name: "Authentication", url: "/my/authentication" },
                { name: "Login Log", url: "/my/login-log" },
                { name: "Logout", url: "/logout" },
            ],
            user_info: null,
        };
    },
    methods: {
        init() {
            if (window.localStorage) {
                let userInfo = window.localStorage["userInfo"];
                if (userInfo) {
                    this.g.userInfo = JSON.parse(window.atob(userInfo));
                    // console.log(this.g.userInfo.ts, new Date().getTime(), this.g.userInfo.ts < new Date().getTime())
                    if (this.g.userInfo.ts < new Date().getTime()) {
                        window.localStorage.clear();
                        this.$EventBus.$emit("logoutComplete");
                        window.document.location.reload();
                        return
                    }
                    this.user_info = this.g.userInfo;
                }
            }
            this.$EventBus.$on("loginComplete", (userInfo) => {
                this.g.userInfo = userInfo;
                this.user_info = this.g.userInfo;
                if (window.localStorage) {
                    // 登录状态保存 24 小时
                    userInfo.ts = new Date().getTime() + (1000 * 60 * 60 * 24)
                    window.localStorage["userInfo"] = window.btoa(JSON.stringify(userInfo));
                }
            });
            this.$EventBus.$on("logoutComplete", () => {
                this.g.userInfo = null;
                this.user_info = null;
                window.localStorage.clear();
            });
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.icon-style >>> svg {
    vertical-align: 0em;
}
</style>
