<template>
    <div style="background-color: #ececec; padding: 20px;">
        <a-spin v-if="loading" />
        <a-row v-if="!loading" :gutter="10">
            <a-col v-for="item, i in list" :key="i" :span="4" class="t-left shadow-sm">
                <a-card v-if="i<6" :title="item.symbol + '/USDT'" :bordered="false">
                    <a-row type="flex" justify="space-around" align="middle" :class="item.changePercent24Hr>0 ? 't-green' : 't-red'">
                        <a-col :span="12" class="t-big">
                            ${{round(item.priceUsd)}}
                        </a-col>
                        <a-col :span="12" class="t-right">
                            {{round(item.changePercent24Hr, 100)}}%
                        </a-col>
                    </a-row>
                </a-card>
            </a-col>
        </a-row>
    </div>
</template>

<script>
export default {
    name: "Bg020",
    components: {
    },
    data() {
        return {
            loading: false,
            list: [],
        };
    },
    methods: {
        round(num, length = 1000000) {
            return Math.round(num * length) / length;
        },
        getClassName(item) {
            if (item.changePercent24Hr > 0) {
                return "up";
            } else if (item.changePercent24Hr < 0) {
                return "down";
            }
            return "none";
        },
        showList(res) {
            let t = this;
            let ts = Math.floor(new Date().getTime() / 1000 / 100) * 100;
            let a = res.data.value;
            a.forEach(function (item) {
                let symbol = item.symbol.toLocaleLowerCase();
                item.img = `https://assets.coincap.io/assets/icons/${symbol}@2x.png`;
                item.img7x24 = `https://images.cryptocompare.com/sparkchart/${item.symbol}/USD/latest.png?ts=${ts}`;
                item.class = t.getClassName(item);
            });
            this.list = a;
            this.$EventBus.$emit("homeMarketListDataComplete", a);
        },
        initData() {
            this.loading = true;
            $.get(
                `${this.g.apiUrl}/api/caches?key=coincap-coin-list`,
                (res) => {
                    this.loading = false;
                    if (res.flag != "ok") {
                        //提示错误
                        return;
                    }
                    this.showList(res);
                }
            );
        },
        init() {
            this.initData()
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.chang24hr {
    border-radius: 5px;
    padding: 5px;
    text-align: center;
}

.up {
    background: #d4f6f0;
    color: #0d8551;
}

.down {
    color: #ef4034;
    background: #ffe4e4;
}

.none {
    color: #6b6b6b;
    background: #ffffff;
}
</style>
