<template>
    <div class="container" style="margin-top:60px;">
        <table class="table">
            <thead class="t-gray">
                <td widtd="80">#</td>
                <td widtd="180">名称</td>
                <td widtd="280">价格</td>
                <td>涨跌</td>
                <td>行情</td>
                <td>交易</td>
            </thead>
            <tbody>
                <tr v-for="item in list" :key="item.id" style="font-size:1rem; color:#666;">
                    <td>
                        <img :src="item.img" alt="Bitcoin" width="36px">
                    </td>
                    <td class="t-left">
                        <a-row>{{item.symbol}}</a-row>
                        <a-row>{{item.id}}</a-row>
                    </td>
                    <td>${{round(item.priceUsd)}}</td>
                    <td>
                        <div :class="item.class" class="chang24hr">{{round(item.changePercent24Hr)}}</div>
                    </td>
                    <td><img :src="item.img7x24" alt="img"></td>
                    <td>
                        <a-button>购买</a-button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    name: "Bg010",
    components: {
    },
    data() {
        return {
            loading: false,
            list: [],
        };
    },
    methods: {
        round(num, length = 1000000) {
            return Math.round(num * length) / length;
        },
        getClassName(item) {
            if (item.changePercent24Hr > 0) {
                return "up";
            } else if (item.changePercent24Hr < 0) {
                return "down";
            }
            return "none";
        },
        showList(res) {
            let t = this;
            let ts = Math.floor(new Date().getTime() / 1000 / 100) * 100;
            let a = res.data.value;
            a.forEach(function (item) {
                let symbol = item.symbol.toLocaleLowerCase();
                item.img = `https://assets.coincap.io/assets/icons/${symbol}@2x.png`;
                item.img7x24 = `https://images.cryptocompare.com/sparkchart/${item.symbol}/USD/latest.png?ts=${ts}`;
                item.class = t.getClassName(item);
            });
            this.list = a;
            // this.$EventBus.$emit("homeMarketListDataComplete", a);
        },
        initData() {
            this.loading = true;
            $.get(
                `${this.g.apiUrl}/api/caches?key=coincap-coin-list`,
                (res) => {
                    this.loading = false;
                    if (res.flag != "ok") {
                        //提示错误
                        return;
                    }
                    this.showList(res);
                }
            );
        },
        init() {
            let temp = setTimeout(() => {
                this.initData()
            }, 1000)

            this.loading = true;
            this.$EventBus.$on("homeMarketListDataComplete", (arr) => {
                this.loading = false;
                this.list = arr
                clearTimeout(temp)
            });
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.chang24hr {
    border-radius: 5px;
    padding: 5px;
    text-align: center;
}

.up {
    background: #d4f6f0;
    color: #0d8551;
}

.down {
    color: #ef4034;
    background: #ffe4e4;
}

.none {
    color: #6b6b6b;
    background: #ffffff;
}
</style>
